/* Fonts */

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Italic.eot");
  src: url("../fonts/Winston/Winston-Italic.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Italic.woff2") format("woff2"), url("../fonts/Winston/Winston-Italic.woff") format("woff"), url("../fonts/Winston/Winston-Italic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Regular.eot");
  src: url("../fonts/Winston/Winston-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Regular.woff2") format("woff2"), url("../fonts/Winston/Winston-Regular.woff") format("woff"), url("../fonts/Winston/Winston-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Bold.eot");
  src: url("../fonts/Winston/Winston-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Bold.woff2") format("woff2"), url("../fonts/Winston/Winston-Bold.woff") format("woff"), url("../fonts/Winston/Winston-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* Variables */

:root {
  --color-white: #FFFFFF;
  --color-black: #000000;
  --color-dark: #3E3E3E;
  --text-red-gradient: linear-gradient(208.77deg, #A42E32 -24.41%, #E52A31 92.24%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  --gradient-btn: linear-gradient(208.77deg, #A42E32 -24.41%, #E52A31 92.24%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  --color-red: #A42E32;
  --color-blue: #00b2ff;
  --invalid: #f30008;
  --color-yellow: #dcc101;
  --color-green: #8ABC36;
  --font-winston: "Winston", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

img {
  max-width: 100%;
}

/* Animation */

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-revert {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

@keyframes lines-transform {
  from {
    transform: translateX(-5%);
  }

  to {
    transform: translateX(-32%);
  }
}

/* Burger-menu */

@keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

/* Spinner */

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.gs-reveal {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease-in-out 0.2s, visibility 0.5s ease-in-out 0.2s;
  will-change: opacity, visibility;
}

.gs-reveal.is-revealed {
  opacity: 1;
  visibility: visible;
}

.a-reveal-left {
  transform: translate3d(-100px, 0, 0);
  opacity: 0;
  transition: transform ease-in-out 0.5s 0.5s, opacity 0.5s ease-in-out 0.5s;
}

.a-reveal-left.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-right {
  transform: translate3d(100px, 0, 0);
  opacity: 0;
  transition: transform ease-in-out 0.5s 0.5s, opacity 0.5s ease-in-out 0.5s;
}

.a-reveal-right.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-element {
  transform: translate3d(0, -50px, 0);
  opacity: 0;
  transition: transform ease-out 0.5s 1.3s, opacity 0.5s ease-out 1.3s;
}

.a-reveal-element.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-opacity {
  opacity: 0;
  transition: opacity 0.5s ease-out 1.3s;
}

.a-reveal-opacity.is-revealed {
  opacity: 1;
}

