/* Debug */

/* Containers */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.container-full {
  padding-left: 30px;
  padding-right: 30px;
}

/* Hide */

.hide {
  display: none !important;
}

/* Position */

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

/* Background */

.bg--white {
  background-color: var(--color-white);
}

.bg--blue {
  background-color: var(--color-blue);
}

.bg--light-blue {
  background-color: var(--color-light-blue);
}

.block-padding {
  padding: 75px 0;
}

.block-padding-large {
  padding: 120px 0 150px;
}

.block-padding-small {
  padding: 50px 0 50px;
}

/* Links */

.link {
  text-decoration: none;
  transition: ease 0.3s;
  color: var(--color-dark);
}

.link:hover {
  color: var(--color-red);
}

/* Title */

.title {
  font-family: var(--font-winston);
  font-style: normal;
  margin: 0;
}

.title--big {
  font-size: calc(30px + 20 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--big {
    font-size: 50px;
  }
}

@media screen and (max-width: 360px) {

  .title--big {
    font-size: 30px;
  }
}

.title--large {
  font-size: calc(30px + 10 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--large {
    font-size: 40px;
  }
}

@media screen and (max-width: 360px) {

  .title--large {
    font-size: 30px;
  }
}

.title--medium {
  font-size: calc(20px + 5 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--medium {
    font-size: 25px;
  }
}

@media screen and (max-width: 360px) {

  .title--medium {
    font-size: 20px;
  }
}

.title--tiny {
  font-size: 18px;
}

.title--black {
  color: var(--color-black);
}

.title--white {
  color: var(--color-white);
}

.title--red-gradient {
  background: var(--text-red-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title--w-bold {
  font-weight: bold;
}

.title--uppercase {
  text-transform: uppercase;
}

.title--indent-small {
  margin-bottom: 30px;
}

.title--indent-large {
  margin-bottom: 60px;
}

/* Text */

.text {
  font-family: var(--font-winston);
  font-style: normal;
  margin: 0;
}

.text--black {
  color: var(--color-black);
}

.text--white {
  color: var(--color-white);
}

.text--dark {
  color: var(--color-dark);
}

.text--medium {
  font-size: calc(16px + 4 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px) {

  .text--medium {
    font-size: 20px;
  }
}

@media screen and (max-width: 1024px) {

  .text--medium {
    font-size: 16px;
  }
}

.text--normal {
  font-size: calc(14px + 2 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px) {

  .text--normal {
    font-size: 16px;
  }
}

@media screen and (max-width: 1024px) {

  .text--normal {
    font-size: 14px;
  }
}

.text--small {
  font-size: 12px;
}

.text--w-regular {
  font-weight: normal;
}

.text--w-bold {
  font-weight: 700;
}

.text--italic {
  font-style: italic;
}

.text--uppercase {
  text-transform: uppercase;
}

