.hystmodal__opened {
  position: fixed;
  right: 0;
  left: 0;
  top: auto;
  overflow: hidden;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--moved,
.hystmodal--active {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
  min-height: 100%;
  margin: auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}

.hystmodal__window {
  margin: 50px 0;
  box-sizing: border-box;
  flex-shrink: 0;
  flex-grow: 0;
  overflow: visible;
  transition: opacity 0.2s ease 0.3s;
  opacity: 0;
}

.hystmodal--active .hystmodal__window {
  opacity: 1;
}

